import { Component, OnInit } from '@angular/core';
declare var jQuery: any;


@Component({
  selector: 'app-teamtaskcards',
  templateUrl: './teamtaskcards.component.html',
  styleUrls: ['./teamtaskcards.component.css']
})
export class TeamtaskcardsComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    //Jquery here
//     // Get the modal
var modal = document.getElementById("myModal");

// Get the image that opens the modal
var imgg = document.getElementById("myimg");

// Get the <span> element that closes the modal
var span = document.getElementsByClassName("close")[0];

// When the user clicks the image, open the modal 
imgg.onclick = function() {
  modal.style.display = "block";
}

// When the user clicks on <span> (x), close the modal
 function trigger(){
  modal.style.display = "none";
}

// When the user clicks anywhere outside of the modal, close it
window.onclick = function(event) {
  
  if (event.target == modal) {
    modal.style.display = "none";
    console.log(event.target)
  }
}

// Initialise Modal  function
   // Toggle modal

    window.addEventListener("load", modaControl)
    function modaControl(){
      console.log("loaded");
      let modaToggle = document.querySelectorAll(".moda_toggle");
      let moda = document.querySelector(".moda");
      let overlay = document.querySelector(".overlay");
      var close = modal.querySelector("#cancel");
      modaToggle.forEach((toggle)=>{
        toggle.addEventListener("click", lunchModa);
        function lunchModa(){
          modaSwitch.show()
        };
        // hide modal when user clicks the close icon
        close.addEventListener("click", function(){
          modaSwitch.hide();
          console.log("clicked")
        });
      });
      // modal modes
      var modaSwitch = {
        show : ()=>{
          overlay.classList.remove("hidden");
          moda.classList.remove("hidden");
        },
        hide : ()=>{
          overlay.classList.add("hidden");
          moda.classList.add("hidden");
        }
      };

      // Hide modal when user clicks outside the modal

      window.addEventListener('click', function(event){
        if (event.target === overlay) {
          modaSwitch.hide();
        }
      })
    };

  }


  

}
